<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">轮播图管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/rotation/detail', query: { pageState: 1 }}"> 添加轮播图</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="tableData"
                stripe
                style="width: 100%"
                @row-dblclick="rowDbclick">
        <el-table-column prop="date"
                        label="图片"
                        min-width="150">
          <template slot-scope="scope">
            <img :src="scope.row.image" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="city"
                        label="类别"
                        min-width="150">
        </el-table-column>
        <el-table-column label="操作"
                        min-width="100">
          <template slot-scope="scope">
            <el-button @click="editHandleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      tableData: [],
      curPage: 1,
      pageSize: 20,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
    },

    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
    },

    // 编辑操作
    editHandleClick (row) {
      this.$router.push({
        path: '/rotation/detail',
        query: {
          pageState: 2
        }
      })
    },

    // 删除操作
    deleteHandleClick (row) {
    },

    // 表单双击
    rowDbclick (row) {
      this.$router.push({
        path: '/rotation/detail',
        query: {
          pageState: 3,
          slider_id: row.id
        }
      })
    }
  }
}
</script>

<style>
</style>
